"use client";

import { useEffect, useState } from "react";
import Product from "../types/product";
import productService from "../services/product";
import ListResponse from "../types/listResponse";
import { useSearchParams } from "next/navigation";
import PageInfo from "../types/pageInfo";
import { extractFilterParams, queryStringToObject, transformObject } from "../utils/filter";
import { SortProduct } from "../constants/common";

function useProducts() {
    const searchParams = useSearchParams();
    const [products, setProducts] = useState<Product[]>([]);
    const [hasLoadAll, setHasLoadAll] = useState<boolean>(false);
    const [pageInfo, setPageInfo] = useState<PageInfo | null>(null);
    const [isLoading, setIsLoading] = useState<boolean>(false);

    const searchKeyword: string | null = searchParams.get("search") ?? "";
    const queryParams: string | null = searchParams.toString();
    let cursor: string | null = pageInfo?.endCursor ?? null;

    useEffect(() => {
        clearData();
        getProducts();
    }, [queryParams]);

    const getProducts = async () => {
        setIsLoading(true);

        const filterParams: string = extractFilterParams(searchParams.toString());
        const filterVariables: Record<string, any> = queryStringToObject(filterParams);
        const sortParams: SortProduct | null = searchParams.get("sort") as SortProduct;

        const transformFilterVariables: Record<string, any> = transformObject(filterVariables, {
            asNumbers: ["filter.price.min", "filter.price.max"],
        });

        const response: ListResponse<Product> | undefined = await productService.getProducts(
            cursor,
            searchKeyword,
            transformFilterVariables,
            sortParams,
        );

        if (response) {
            setProducts((prev) => [...prev, ...response.nodes]);
            setPageInfo(response.pageInfo);
            setHasLoadAll(!response.pageInfo.hasNextPage);
        }
        setIsLoading(false);
    };

    const clearData = () => {
        cursor = null;
        setPageInfo(null);
        setProducts([]);
        setHasLoadAll(false);
    };

    return { products, getProducts, hasLoadAll, isLoading };
}

export default useProducts;
